<template>
  <v-container class="py-8 px-6">
    <v-card class="pa-5">
      <v-card-title class="mb-5 font-weight-bold">유저정보</v-card-title>
      <v-data-table
        :headers="headers"
        :items="items"
        show-expand
        single-expand
        expand-icon="mdi-pencil"
        show-select
        v-model="selected"
      >
        <template v-slot:top>
          <v-row align="center">
            <v-col cols="1">
              <v-btn small text>삭제</v-btn>
            </v-col>
            <v-spacer></v-spacer>
            <v-col cols="4">
              <v-dialog>
                <template v-slot:activator="{on,attrs}">
                  <v-btn small v-bind="attrs" v-on="on">신규등록</v-btn>
                </template>
                <v-card class="pa-5" height="250px">
                  <v-card-title>신규등록</v-card-title>
                  <v-row>
                    <v-col cols="3">
                      <v-menu
                        ref="menu"
                        v-model="menu"
                        :close-on-content-click="false"
                        :close-on-click="false"
                        :return-value.sync="date"
                        transition="scale-transition"
                        offset-y
                        min-width="auto"
                      >
                        <template v-slot:activator="{ on, attrs }">
                          <v-text-field
                            v-model="date"
                            label="Picker in menu"
                            prepend-icon="mdi-calendar"
                            readonly
                            v-bind="attrs"
                            v-on="on"
                          ></v-text-field>
                        </template>
                        <v-date-picker locale="ko" v-model="date" no-title scrollable>
                          <v-btn text color="primary" @click="menu = false">Cancel</v-btn>
                          <v-btn text color="primary" @click="$refs.menu.save(date)">OK</v-btn>
                        </v-date-picker>
                      </v-menu>
                    </v-col>
                    <v-col cols="3">
                      <v-text-field label="이름"></v-text-field>
                    </v-col>
                    <v-col cols="3">
                      <v-select label="부서" :items="['가','나','다']"></v-select>
                    </v-col>
                    <v-col cols="3">
                      <v-checkbox label="근무여부"></v-checkbox>
                    </v-col>
                  </v-row>
                  <v-card-actions class="pr-5">
                    <v-spacer></v-spacer>
                    <v-btn>취소</v-btn>
                    <v-btn>제출</v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-col>
          </v-row>
        </template>
        <!-- <template v-slot:header>추가</template> -->
        <template v-slot:item.regDate="{item}">{{item.regDate | formatDate}}</template>
        <template v-slot:item.isWorking="{item}">
          <v-chip :color="item.isWorking? 'primary':'default'">{{item.isWorking? '근무중':'퇴사'}}</v-chip>
        </template>
        <template v-slot:expanded-item>여기에서 수정하고 싶어요.</template>
        <template v-slot:item.actions="{item}">
          <v-icon @click="deleteItem(item)">mdi-delete</v-icon>
        </template>
      </v-data-table>
    </v-card>
  </v-container>
</template>

<script>
import { userList } from "@/mock";
import { formatDate } from "@/utils/filter";

export default {
  components: {},
  data() {
    return {
      menu: false,
      date: "2020-11-13",
      selected: [],
      headers: [
        { text: "등록번호", value: "id" },
        { text: "이름", value: "name" },
        { text: "부서", value: "department" },
        { text: "등록일", value: "regDate" },
        { text: "근무여부", value: "isWorking" },
        { text: "", value: "data-table-expand" },
        { text: "", value: "actions" }
      ],
      items: userList
    };
  },
  methods: {
    deleteItem(item) {
      alert(JSON.stringify(item));
      console.log(this.selected);
    }
  },
  filters: {
    formatDate
  }
};
</script>

<style>
</style>